import React, { Component } from 'react'

import  request  from '../../api'

import Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'

class A extends Component{

    username=()=>{
        console.log(this.input2.value)
        Pubsub.publish('value',this.input2.value)
    }

    render(){
        return(
            <fieldset className="form-group">
                <input onBlur={this.username}  ref={(i)=>{this.input2 = i}} className="form-control form-control-lg" type="text" placeholder="Your Name"/>
            </fieldset>
        )
    }
}
export default class Login extends Component { 

    state ={
        username:"",
        email:'',
        password:''
    }
  /* 
    用户名不能太短，或者重复 
    正确格式的邮箱
    密码过于简单  ，太短（最少8个字符）
  */


      //判断是注册还是登陆  register   Sign up(注册)   login   Sign in(登录)
      isLogin(){
        return this.props.match.path === '/login'
     }


    componentDidMount(){
        Pubsub.subscribe('value',(msg,data)=>{
            this.setState({
                username : data
            })
        })

       
    }
    

    onsubmit=(e)=>{
        // 阻止默认行为
        e.preventDefault();

        // let user = {
        //     username: this.state.username ? this.state.username : '',
        //     email : this.state.email,
        //     password : this.state.password
        // }

        request({
            method:'POST',
            url:this.state.username ? '/api/users' : "/api/users/login",
            data:{
                "user":{
                    'username': this.state.username ? this.state.username : '',
                    'email' : this.state.email,
                    'password' : this.state.password
                    }
            }
        }).then((res)=>{
            console.log('login res',res)
            // console.log(res.data.user.token)
            // Pubsub.publish('info',res.data)
            // 获取到token 存放到Cookie中
            Cookie.set('auth', res.data.user.token)

            //路由跳转
            this.props.history.push('/home')

            // 发布内容
            Pubsub.publish('auth',res.data.user.token)
            // 发布个人信息
            Pubsub.publish('info',res.data.user)
        })
    }
    
    // 邮箱输入框 (回调函数的ref 获取input值)
    email=()=>{
        // console.log(this.input1.value)
        this.setState({
            email : this.input1.value
        })
    }
    // 密码输入框 (通过event.target.value 获取input值)
    password=(e)=>{
        // console.log(e.target.value)
        this.setState({
            password : e.target.value
        })
        
    }
    render() {
        // console.log(this.state)
        return (
            <div>
                <div className="auth-page">
                    <div className="container page">
                        <div className="row">
                        <div className="col-md-6 offset-md-3 col-xs-12">
                            <h1 className="text-xs-center">
                                {this.isLogin() ? 'Sign in ' : 'Sign up'}
                                </h1>
                            <p className="text-xs-center">
                            <a href="/">
                                {this.isLogin() ? 'Need an account?' : 'Have an account?'}
                                </a>
                            </p>
                            <ul className="error-messages">
                            <li>That email is already taken</li>
                            </ul>
                            <form onSubmit={this.onsubmit}> 
                                {this.isLogin() ? '' : <A/>}
                                <fieldset  className="form-group">
                                    <input onBlur={this.email} ref={(i)=>{this.input1 = i}} className="form-control form-control-lg" type="text" placeholder="Email"/>
                                </fieldset>
                                <fieldset className="form-group">
                                    <input onBlur = {this.password} className="form-control form-control-lg" type="password" placeholder="Password" />
                                </fieldset>
                                <button className="btn btn-lg btn-primary pull-xs-right">
                                    {this.isLogin() ? 'Sign in ' : 'Sign up'}
                                </button>
                            </form>
                        </div>

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
