import React, { Component } from 'react'
// import axios from 'axios'


import request from '../../api'

import { addFavorite , deleFavorite } from '../../api/favorited'

// import { NavLink} from 'react-router-dom'
export default class Home extends Component {
  state = {
    data:{
      articles:[],
    },
    tags:{
      tags:[]
    },

    PageCount:'',
    // 每页条数
    limit: 10,
    offset:0,

    articlesClass:0,
    
    favoritesDisabled : false

    
  }

  componentDidMount(){
    request({
      method : 'get',
      url:'/api/articles',
      params:{
        limit: this.state.limit,
      }
    }).then((res)=>{
      // console.log('res1',res)
      const page = res.data.articlesCount / res.data.articles.length
      const PageCount = res.data.articlesCount % res.data.articles.length === 0 ? page : page + 1
      if(res.status === 200){
        this.setState({
          data: res.data,
          PageCount : parseInt(PageCount)
        })
      }else{
        console.log(res.statusText)
      }
    })


    request({
      method : 'get',
      url:'/api/tags',
      params:{}
    }).then((res)=>{
      // console.log('res',res)
      if(res.status === 200){
        this.setState({
          tags: res.data
        })
      }else{
        console.log(res.statusText)
      } 
    })
  }

  onclick=(id)=>{
    // console.log(id);
     
    this.setState({
      articlesClass : id
    })

    request({
      method : 'get',
      url:'/api/articles',
      params:{
        limit: this.state.limit,
        offset: id * this.state.limit
      }
    }).then((res)=>{
      if(res.status === 200){
        this.setState({  
          data: res.data
        })
      }else{
        console.log(res.statusText)
      }
    })
  }
  // 点赞功能
  async favorited(data){
    console.log('data',data)
    await this.setState({
      favoritesDisabled:true
    })

    if(data.favorited){
        // 取消点赞
        await deleFavorite(data).then((res)=>{
          console.log('取消点赞res',res)
        })
        data.favorited = false
        data.favoritesCount += -1

        const key = this.state.data.articles.indexOf(data)
        // console.log(this.state.data.articles)
        this.state.data.articles[key] = data
        await this.setState({
          articles: this.state.data.articles
        })
        
    }else{
        // 点赞
        await addFavorite(data).then((res)=>{
          console.log('点赞res',res)
        })
        data.favorited = true
        data.favoritesCount += 1

        const key = this.state.data.articles.indexOf(data)
        // console.log(this.state.data.articles)
        this.state.data.articles[key] = data
        await this.setState({
          articles: this.state.data.articles
        })


    }
    await this.setState({
      favoritesDisabled:false
    })
   
  }
  
    render() {
      const { PageCount } = this.state
        return (

          <div className="home-page">
            <div className="banner">
              <div className="container">
                <h1 className="logo-font">conduit</h1>
                <p>A place to share your knowledge.</p>
              </div>
            </div>
            
            <div className="container page">
              <div className="row">
          
                <div className="col-md-9">
                  <div className="feed-toggle">
                    <ul className="nav nav-pills outline-active">
                      <li className="nav-item">
                        <a className="nav-link disabled" href="/">Your Feed</a>
                      </li>
                      <li className="nav-item">
                        <a className="nav-link active" href="/">Global Feed</a>
                      </li>
                    </ul>
                  </div>

                {

                  this.state.data.articles.map((item,index)=>{
                    return(
                      <div className="article-preview" key={index}>
                        <div className="article-meta">
                          <a href="profile.html"><img src={item.author.image} alt=''/></a>
                          <div className="info">
                            <a href="/" className="author">{item.author.username}</a>
                            <span className="date">{item.createdAt}</span>
                          </div>
                          <button className="btn btn-outline-primary btn-sm pull-xs-right"
                                  onClick = {()=>{this.favorited(item)}}
                                  disabled = {this.state.favoritesDisabled}
                          >
                            <i className="ion-heart"></i> {item.favoritesCount}
                          </button>
                        </div>
                        <a href="/" className="preview-link">
                          <h1>{item.title}</h1>
                          <p>{item.description}</p>
                          <span>Read more...</span>
                        </a>
                      </div>
                    )
                  })
                }
                <ul className='pagination'>
                    {
                      new Array(PageCount).fill().map((item,index)=>{
                        return (
                          <li key={index} 
                          onClick = {() => {this.onclick(index)}} 
                          className={this.state.articlesClass === index ? 'page-item ng-scope active' : 'page-item ng-scope'}>

                          <span className="page-link ng-binding" >{index + 1}</span>
                        </li>
                        )
                      })
                    } 
                </ul>

                </div>
                <div className="col-md-3">
                  <div className="sidebar">
                    <p>Popular Tags</p>
                    <div className="tag-list">
                      {
                        this.state.tags.tags.map((item,index)=>{
                         return(
                          <a href="/" className="tag-pill tag-default" key={index}>{item} </a>
                         )
                        })
                      }
                      
                    </div>
                  </div>
                </div>
              </div>
             
            </div>
          </div>
        )
    }
}
