import React, { Component } from 'react'

import { Route ,Switch , Redirect} from "react-router-dom";

import Header from './Header'
import Footer from './Footer'
import Home from './pages/Home'
import Article from './pages/Article'
import EditArticle from './pages/EditArticle'
import Login from './pages/Login'
import Settings from './pages/Settings'
import Profile from './pages/Profile'
import Cookie from 'js-cookie'

export default class App extends Component {
  render() {
    if(Cookie.get("auth")){
      return (
        <div>
            <Header/>
                <Switch>
                    <Route  path='/home' component={Home}></Route>  {/* 首页*/}
                    <Route  path='/editor' component={EditArticle}></Route>
                    <Route  path='/register' component={Home}></Route>  {/* 注册页面*/}
                    <Route  path='/login' component={Home}></Route>  {/* 登录页面*/}
                    <Route  path='/article' component={Article}></Route>
                    <Route  path='/settings' component={Settings}></Route>
                    <Route  path='/profile' component={Profile}></Route>
                    <Redirect to='/home'/>
                </Switch>
            <Footer/>
        </div>
      )
    }else{
      return (
        <div>
            <Header/>
                <Switch>
                    <Route  path='/home' component={Home}></Route>
                    <Route  path='/editor' component={Home}></Route>
                    <Route  path='/register' component={Login}></Route>
                    <Route  path='/login' component={Login}></Route>
                    <Route  path='/article' component={Home}></Route>
                    <Route  path='/settings' component={Home}></Route>
                    <Route  path='/profile' component={Home}></Route>
                    <Redirect to='/home'/>
                </Switch>
            <Footer/>
        </div>
      )
    }    
  }
}

