import React, { Component } from 'react'
import '../main.css'
import { NavLink} from 'react-router-dom'
import Cookie from 'js-cookie'
import PubSub from 'pubsub-js'

export default class Header extends Component {

        state={
            auth:'',  //传过来的token令牌
            user: {}   //用户信息
        }
    componentDidMount(){

        PubSub.subscribe('auth',(msg,data)=>{
            // console.log(data)
            this.setState({
                auth : data
            })
        })

        PubSub.subscribe('info',(msg,data)=>{
            console.log('info',data)
            this.setState({
                user : data
            })
        })

    }
    render() {
        const {user } = this.state
        console.log('this.state.user',this.state.user)
        console.log(user.image,user.username)
        
        if(Cookie.get('auth')){
            return (
                <div>
                    <nav className="navbar navbar-light">
                        <div className="container">
                            <a className="navbar-brand" href="index.html">conduit</a>
                            <ul className="nav navbar-nav pull-xs-right">
                                <li className="nav-item">
                                    <NavLink  className="nav-link active" to="/home">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName='active' className="nav-link" to="/editor">
                                        <i className="ion-compose"></i>&nbsp;New Article
                                    </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName='active' className="nav-link" to="/settings">
                                        <i className="ion-gear-a"></i>&nbsp;Settings
                                    </NavLink>
                                </li>
                              
                              <li className="nav-item"> 
                                  <NavLink activeClassName='active' className="nav-link ng-binding active" ui-sref-active="active" ui-sref="app.profile.main" to="/profile" href="#/@mjq">
                                        <img ng-src="https://z3.ax1x.com/2021/04/28/gPpELR.jpg" src={user.image} className="user-pic" alt="" />
                                         {user.username}
                                   </NavLink> 
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            )
        }else{
            return (
                <div>
                    <nav className="navbar navbar-light">
                        <div className="container">
                            <a className="navbar-brand" href="index.html">conduit</a>
                            <ul className="nav navbar-nav pull-xs-right">
                                <li className="nav-item">
                                    <NavLink  className="nav-link active" to="/home">Home</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName='active' className="nav-link" to="/login">Sign in</NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink activeClassName='active' className="nav-link" to="/register">Sign up</NavLink>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            )
        }  
    }
}
